import { useEffect } from "react";

export const joinRegexMsg = (text) => {
    let message ='';
    if(text === 'ID') message = '   '
    if(text === 'Password') message = '8  , ,  '
    if(text === 'ConfirmPassword') message = '  '
    if(text === 'Name') message = ',   '
    if(text === 'Phone') message = '    ' 
    return <div style={{ m: 1, fontSize:'12px',fontWeight:'bold', color:'red', width: '80%', margin:'-7px auto' }} >
    {message}
  </div>
}