export const decode = (token) => {
    const base64Url = token.split('.')[1]; //  
    const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/'); // URL-safe Base64  Base64 
    const jsonPayload = decodeURIComponent(escape(window.atob(base64))); // Base64   URI 
    return JSON.parse(jsonPayload); // JSON  
}